#content-area {
  margin-top: 80px;
}
.module-dashing {
  border: 2px dashed #C9CEC8;
}
.module-border-bottom {
  border-bottom: 1px dashed #C9CEC8;
}
.module {
  color: #555;
  font-size: 20px;
  margin-bottom: 20px;
}
.module:hover {
  background: #F2F2E9;
  cursor: pointer;
}
.module:focus {
  background: transparent;
}
.module-content,
.module-title {
  color: #555;
  font-size: 20px;
  margin-bottom: 20px;
}
.module-content:hover,
.module-title:hover {
  background: #F2F2E9;
  cursor: pointer;
}
.module-content:focus,
.module-title:focus {
  background: transparent;
}
.module-title {
  font-size: 40px;
  padding: 20px;
}
.module-content {
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 10px;
  width: 90%;
  float: left;
}
.module-controls {
  width: 10%;
  float: right;
}
.module-controls.hidden:hover {
  display: block;
}
.module-controls .grabbable:hover {
  cursor: grab;
}
.module-controls .removeable:hover {
  cursor: pointer;
}
.module-container {
  float: left;
  width: 100%;
}
.module-helper-text {
  color: #999;
}
